import { clap, alternate } from './yee';
import copy from 'copy-to-clipboard';
function yeet(value) {
    document.getElementById('yeetext').innerHTML = yeetThatText(value);
}
function optionToggled() {
    var yeetElement = document.getElementById('input-text');
    yeet(yeetElement.value);
}
function yeetThatText(text) {
    var yeetSelected = getCheckboxValue('yeet');
    var alternateSelected = getCheckboxValue('aLtErNaTiNg');
    if (yeetSelected && alternateSelected) {
        return alternate(clap(text));
    }
    else if (yeetSelected) {
        return clap(text);
    }
    else {
        return alternate(text);
    }
}
function copyClicked() {
    copy(document.getElementById('yeetext').textContent);
    document.getElementById('copy').innerText = 'Copied!';
}
function getCheckboxValue(elementId) {
    return document.getElementById(elementId).checked;
}
export { yeet, copyClicked, optionToggled };
